import { shortcut } from '@engine/core';
import { ReportChartBeta } from './chart_beta';

@shortcut()
export class ReportChartGamma extends ReportChartBeta {
    public static xtype = 'report.chart_gamma';

    public static VERSION = '1.0.0';

    public static BUILD_TIME = '2020-11-2 17:32:05';

    protected removeChart() {
        this.chart?.remove();
    }

    public refresh() {
        // CHART-16896 图表的refresh方法是空的，没啥用，和图表沟通后直接用resize方法即可。
        this.chart?.resize();
    }
}
